.privacy-policy {
	margin: -1em 0 -4em;
	padding: 0 1em;
	position: relative;
	z-index: 3;

	&__title {
		margin: 0;
		padding: 0;
		font-weight: 400;
		font-size: 15px;
		line-height: 17px;
		text-align: center;
	}
	&__text {
		font-weight: 400;
		font-size: 12px;
		line-height: 16px;
		text-align: center;
		opacity: 0.5;
		&--bottom {
			padding: 0 12%;
		}
	}
}
